.other-img{
    margin-top: -30em;
    margin-left: -36px;
    width: 12em;
}
.other-img .row {
    display: grid;
}
.other-img li {
    list-style: none;
}
.other-img li img{
    max-width: 130px;
    padding: 10px;
    display: flex;
}
.other-img ul li .side-img{
    -webkit-transform: scale(1);
            transform: scale(1);
	transition: -webkit-transform 0.5s ease;
	transition: transform 0.5s ease;
	transition: transform 0.5s ease, -webkit-transform 0.5s ease;
}
.other-img ul li .side-img:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.details-loading {
    margin-top: 10em;
